@import url('https://fonts.googleapis.com/css2?family=Secular+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@300&display=swap');

body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;

}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */


.quiz_type_container {
  max-width: 600px;
  max-height: 500px;
  min-width: 200px;
  min-height: 300px;
  width: 90vw;
  height: 85vh;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 12px;
  transform: translate(-50%,-50%);
  position: absolute;
  top:50%;
  bottom: 0;
  left: 50%;
  right: 0;
  overflow-y: scroll;
  background-color: white;

}

.quiz_type_container::-webkit-scrollbar {
  display: none;
}

.quiz_type_container div {
  padding: 5px;
}


.option select {
  width: 100%;
  height: 35px;
  outline: none;
  border: 2px solid black;
  border-radius: 5px;
  padding: 5px;
}

.play_button {
  width: 100px;
  height: 30px;
  background-color: rgb(136, 192, 136);
  border-radius: 10px;
  text-align: center;
  user-select: none;
  cursor: pointer;
  transform: translate(-50%,-50%);
  position: absolute; 
  bottom: 0;
  left: 50%;
  right: 0;
}

.quiz_container {
  max-width: 600px;
  max-height: 500px;
  min-width: 250px;
  min-height: 300px;
  width: 90vw;
  height: 85vh;
  box-shadow: rgb(0 0 0 / 24%) 0px 3px 8px;
  border-radius: 12px;
  transform: translate(-50%,-50%);
  position: absolute;
  top: 50%;
  bottom: 0;
  display: flex;
  left: 50%;
  right: 0;
  overflow-y: scroll;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  align-content: center;
  background-color: white;
}





.quiz_container::-webkit-scrollbar {
  display: none;
}


.questions {
  width: 100%;
  height: 100px;
  text-align: center;
  word-break: break-word;
  font-size: 25px;
  padding: 5px;
  font-family: 'Secular One', sans-serif;
}

@media only screen and (max-width:540px) {
  .questions {
    font-size: 20px;
    padding: 5px;
  }
}

@media only screen and (max-width:500px) {
  .questions {  
    font-size: 18px;
  }
  
}

@media only screen and (max-width:300px) {
  .questions {  
    font-size: 14px;
  }
  
}

.question {
  font-size: 25px;
  padding: 5px;
}



.answer {
  width: fit-content;
  min-width: 140px;
  max-height: 55px;
  min-height: 30px;
  border-radius: 7px;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  user-select: none;
  text-align: center;
  cursor: pointer;
  font-family: 'Overpass', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s ease-in-out;
}

@media only screen and (max-width:500px) {
  .answer {
    font-size: 15px
  }
}

.answers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  gap: 40px;
  width: 80%;
  align-items: center;
}

.score {
  font-size: 20px;
  font-family: 'Overpass', sans-serif;

  /* margin-top: 100px; */
}


.end_title {
  display: none;
  font-size: 20px;
  padding: 5px;
  width: 150px;
  height: 35px;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  background-color: #86c586;
  border-radius: 10px;
  font-family: 'Secular One', sans-serif;

}